// Fonts

/*
font-family: 'Montserrat', sans-serif;
font-family: 'Poppins', sans-serif;
*/

$font-main: 'Montserrat', sans-serif;


.none{
    display: none;
}

.visually-hidden{
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}

.no-scroll{
    overflow-y: hidden;
}



