.location{
    flex-grow: 1;
    position: relative;
    display: block;
    padding: 34px 20px 26px 95px;
    background-image: url('./../img/location_icon/location.png');
    background-position: 35px 40px;
    background-size: 25px;
    background-repeat: no-repeat;
    pointer-events: all;

    @media (max-width: 1400px) {
        padding: 20px 20px 18px 55px;
        background-position: 15px 22px;
    }

    &::before{
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: rgba(255,255,255,0.1);
        border-radius: 5px;
        filter: blur(3px);
        transition: all 0.2s ease-in;
    }

    &:hover::before{
        opacity: 0.8;
        filter: blur(4px);
    }
}

.location__title{
    font-style: 20px;
    font-weight: 700;
    line-height: 1.2;
    margin-bottom: 4px;

    @media (max-width: 1400px) {
        font-style: 18px;
    }
}

.location__link{
    font-style: 16px;
    font-weight: 400;
    line-height: 1.25;
    opacity: 0.4;
}

.slide__locations{
    display: flex;
    column-gap: 30px;
    width: 70%;
    margin: 0 auto;

    @media (max-width: 1023px) {
        width: 90%;
    }

    @media (max-width: 767px) {
        width: 90%;
        flex-direction: column;
        row-gap: 20px;
    }
}