.see-more{
    position: relative;
    padding-left: 60px;
    color: rgba(255, 255, 255, 0.6);
    font-size: 16px;
    display: block;
    transform: rotate(-90deg) translate(0%, 100%);
    transform-origin: left bottom;

    &::before{
        content: '';
        position: absolute;
        left: 0;
        top: calc(50% - 1.5px);
        width: 38px;
        height: 3px;
        background-color: #fff;
        opacity: 0.3;
    }
}