.slide{
    height: 100vh;
    overflow: hidden;
    padding-bottom: 80px;
    padding-top: 80px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    background: radial-gradient(
        50% 50% at 50% 50%,
        rgba(0,0,0,0) 0%,
        rgba(0,0,0,0.3) 100%,
    ),
     linear-gradient(
        180deg,
        rgba(0,0,0,0.3) 0%, 
        rgba(255,255,255,0) 99.99%,
        rgba(255,255,255,0) 100%);
        
        &--norway{
            background-image: url(./../img/slider/slider__01.jpg);
        }

        &--oae{
            background-image: url(./../img/slider/slider__02.jpg);
        }

        &--ocean{
            background-image: url(./../img/slider/slider__03.jpg);
        }

        &--austria{
            background-image: url(./../img/slider/slider__04.jpg);
        }
  
         background-color: #555;
         background-size: cover;
         background-attachment: fixed;
         background-position: center center;
         background-repeat: no-repeat;

    &::before{
        content: '';
    }

    cursor: grab;

    &:active{
        cursor: grabbing;
    }

    @media (max-width: 1200px) {
        padding-bottom: 80px;
    }

    @media (max-width: 767px) {
        padding-top: 100px;
        padding-bottom: 40px;
    }

}

.slide__header{
    position: relative;
    text-align: center;
}

.slide__tagline{
    position: absolute;
    top: -8px;
    width: 100%;
    font-weight: 500;
    font-size: 1.56vw;
    line-height: 1.23;
    letter-spacing: 0.55em;
    color: rgba(255,255,255,0.7);

    @media (max-width: 767px) {
        font-size: 3vw;
    }
}

.slide__title{
    font-size:11vw;
    font-weight: 700;
    line-height: 1.22;

    @media (max-width: 767px) {
        font-size: 16vw;
    }
}