.header{
    height: 100vh;
    position: relative;
}

.header a {
    color: #fff;
}

.header__content{
    position: absolute;
    z-index: 9;
    top: 50px;
    left: 70px;
    right: 70px;
    bottom: 80px;
    pointer-events: none ;

    @media (max-width: 1200px) {
        top: 40px;
        left: 40px;
        right: 40px;
        bottom: 60px;
    }

    @media (max-width: 767px) {
        top: 30px;
        left: 5%;
        right: 5%;
        bottom: 30px;
    }
}

.header__socials{
    pointer-events: all;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translate(0%, -50%);

    @media (max-width: 1024px) {
        display: none;
    }

    @media (max-width: 767px) {
        display: none;
    }

    @media (max-height: 600px) {
        top: 40%;
    }
}

.header__see-more{
    pointer-events: all;
    position: absolute;
    left: 0;
    bottom: 0;

    @media (max-width: 1024px) {
        display: none;
    }
}

.header__slider-controls{
    pointer-events: all;
    position: absolute;
    right: 0;
    bottom: 0; 

    @media (max-width: 1024px) {
        display: none;
    }
}

.header__scrollbar{
    pointer-events: all;
    position: absolute;
    right: 0;
    top: 50%;
    width: 300px;
    height: 10px;
     transform: rotate(90deg) translate(50%, 0%);
     transform-origin: right top;

     @media (max-width: 1024px) {
        display: none;
    }
}
